// 商城-角色相关接口
import axios from './../fetch'

// 角色列表-分页
export const coreRolePage = params => { return axios.get('/admin/platform/coreRole/page', { params: params }) }

// 角色信息获取
export const coreRoleGet = params => { return axios.get('/admin/platform/coreRole/get', { params: params }) }

// 角色删除
export const coreRoleDelete = params => { return axios.post('/admin/platform/coreRole/delete', params) }

// 角色添加
export const coreRoleSave = params => { return axios.post('/admin/platform/coreRole/save', params) }

// 角色修改
export const coreRoleUpdate = params => { return axios.post('/admin/platform/coreRole/update', params) }



