// FTP管理相关接口
import axios from './../fetch'

// FTP列表(分页)
export const ftpList = params => { return axios.get('/admin/platform/ftp/page', { params: params }) }

// 获取单个FTP
export const ftpGet = params => { return axios.get('/admin/platform/ftp/get', { params: params }) }

// 删除FTP信息
export const ftpDelete = params => { return axios.post('/admin/platform/ftp/deleteIds', params) }

// 新增FTP信息
export const ftpSave = params => { return axios.post('/admin/platform/ftp/save', params) }

// 修改FTP信息
export const ftpUpdate = params => { return axios.post('/admin/platform/ftp/update', params) }



