<template>
  <div>
  </div>
</template>
<script>
import { refundCheck } from '@/api/order/index.js'
export default {
  data() {
    return {
      visible: false,
      addressVisable: false, // 确认按钮对应弹出框的显示隐藏
      addressLoading: false,
      serviceParams: {
        id: '',
        checked: false,
        checkReason: '',
        addressId: ''
      }
    }
  },
  methods: {
    checkService(data, status) { // 响应对应的操作
      // type 售后类型 id 售后id status 是否确认 refundStatus  售后状态
      const type = parseInt(data.orderRefund.refundType)
      const id = data.orderRefund.id
      if (status) { // 同意售后
        if (type === 1) { // 仅退款
          this.refundPrice(id)
        } else { // 退款退货和换货的确认操作
          this.refundConfirm(id)
        }
      } else {
        this.refusedService(id)// 不通过
      }
    },

    refundPrice(id) { // 仅退款操作
      this.$confirm(
        '确认通过后，申请将直接判定为已完成的状态，为避免卖家与平台的争议，建议与买家及卖家协商。货款将退至买家原支付账户中，后续无法撤回，确认是否继续？',
        '系统提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(res => {
          const params = {
            id: id,
            checked: true
          }
          this.refundCheck(params)
        })
        .catch(res => {
          return false
        })
    },
    refusedService(id) { // 拒绝售后申请
      this.$prompt(
        '如确认不通过买家的售后申请，申请将直接判定为不通过，买家无法再进行该售后申请单操作，建议与买家及卖家协商。是否继续？',
        '系统提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputValidator: value => {
            if (value && value !== '') {
              return !(value.length > 140)
            } else {
              return true
            }
          },
          inputType: 'textarea',
          inputErrorMessage: '说明过长',
          inputPlaceholder: '请输入说明原因'
        }
      )
        .then(({ value }) => {
          this.serviceParams.id = id
          this.serviceParams.checked = false
          this.serviceParams.checkReason = value
          this.refundCheck(this.serviceParams)
        })
        .catch(() => {})
    },
    refundConfirm(id) { // 退款退货和换货的确认操作
      this.$confirm(
        '确认通过后，申请将直接判定为待买家发货状态，卖家收货地址采用卖家默认地址，为避免卖家与平台的争议，建议与买家及卖家协商。货款将退至买家原支付账户中，后续无法撤回，确认是否继续？',
        '系统提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(res => {
          const params = {
            id: id,
            checked: true
          }
          this.refundCheck(params)
        })
        .catch(res => {
          return false
        })
    },
    refundCheck(params) { // 发送审核
      refundCheck(params).then(res => {
        this.addressLoading = true
        if (res.code === 200) {
          this.$successMessage('操作成功')
          this.$emit('change', true)
        } else {
          this.$emit('change', false)
          this.$errorMessage(res.message)
        }
        this.addressLoading = false
        this.addressVisable = false
      })
    },
    handleClose() {
      this.addressVisable = false
      this.visible = false
    }
  }
}
</script>

 <style lang="scss" scoped>
.addr-items{
  width: 100%;
 padding: 10px 15px;
  border-bottom: 1px solid #e0e0e0;
}


</style>



