// 商城-管理员相关接口
import axios from './../fetch'

// 管理员列表
export const adminList = params => { return axios.get('/admin/platform/coreUser/page', { params: params }) }

// 管理员详情
export const adminGet = params => { return axios.get('/admin/platform/coreUser/get', { params: params }) }

// 管理员删除
export const adminDelete = params => { return axios.post('/admin/platform/coreUser/delete', params) }

// 禁用管理员
export const adminDisable = params => { return axios.post('/admin/platform/coreUser/disable', params) }

// 启用管理员
export const adminEnable = params => { return axios.post('/admin/platform/coreUser/enable', params) }

// 新增管理员
export const adminSave = params => { return axios.post('/admin/platform/coreUser/save', params) }

// 修改管理员
export const adminUpdate = params => { return axios.post('/admin/platform/coreUser/update', params) }

// 验证用户名
export const validName = params => { return axios.get('/admin/platform/coreUser/validName', { params: params }) }

// 验证邮箱
export const validMail = params => { return axios.get('/admin/platform/coreUser/validMail', { params: params }) }



