// 友情链接相关接口
import axios from './../fetch'

// 友情链接列表
export const friendLinkPage = params => { return axios.get('/admin/platform/link/page', { params: params }) }

// 友情链接查询单个
export const friendLinkGet = params => { return axios.get('/admin/platform/link/get', { params: params }) }

// 友情链接添加
export const friendLinkSave = params => { return axios.post('/admin/platform/link/save', params) }

// 友情链接修改
export const friendLinkUpdate = params => { return axios.post('/admin/platform/link/update', params) }

// 友情链接删除
export const friendLinkDelete = params => { return axios.post('/admin/platform/link/deleteIds', params) }

// 是否开启
export const linkIsEnable = params => { return axios.post('/admin/platform/link/isEnable', params) }

// 批量保存排序值
export const linkSort = params => { return axios.post('/admin/platform/link/sort', params) }



