// 商城-字典相关接口
import axios from './../fetch'
// 字典类型列表分页
export const dictTypeManagerPage = params => { return axios.get('/admin/platform/dictTypeManager/page', { params: params }) }

// 字典类型数据详情
export const dictTypeManagerGet = params => { return axios.get('/admin/platform/dictTypeManager/get', { params: params }) }
// 字典类型数据删除
export const dictTypeManagerDelete = params => { return axios.post('/admin/platform/dictTypeManager/delete', params) }

// 字典类型数据新增
export const dictTypeManagerSave = params => { return axios.post('/admin/platform/dictTypeManager/save', params) }
// 字典类型数据修改
export const dictTypeManagerUpdate = params => { return axios.post('/admin/platform/dictTypeManager/update', params) }

// 字典数据列表分页
export const dictDataManagerPage = params => { return axios.get('/admin/platform/dictDataManager/page', { params: params }) }

// 字典数据详情
export const dictDataManagerGet = params => { return axios.get('/admin/platform/dictDataManager/get', { params: params }) }
// 字典数据删除
export const dictDataManagerDelete = params => { return axios.post('/admin/platform/dictDataManager/delete', params) }

// 字典数据新增
export const dictDataManagerSave = params => { return axios.post('/admin/platform/dictDataManager/save', params) }
// 字典数据修改
export const dictDataManagerUpdate = params => { return axios.post('/admin/platform/dictDataManager/update', params) }

// 字典类型是否存在
export const checkDictType = params => { return axios.get('/admin/platform/dictTypeManager/checkDictType', { params: params }) }

// 字典编码是否存在
export const checkCode = params => { return axios.get('/admin/platform/dictDataManager/checkCode', { params: params }) }



