
<!--关键字选择-->
<template>
  <div>
    <div @click="initData">
      <slot>
        <el-button type="primary" size="mini">关键词选择</el-button>
      </slot>
    </div>
    <el-dialog :visible="visible" :before-close="handleClose" title="关键词选择" :append-to-body='true' width="70%">
      <div style="padding:20px;column-count:3">
        <div v-for="(item) in tableData" :key="item.id" class="keyword-items" :class="isChecked(item) ?'keyword-items--active':'' " @click="setCheckedItems(item)">
          <div class="text-overflow">
            <span>规则名：</span>{{item.ruleName}}</div>
          <span>关键词：</span>
          <template v-for="(wordItem,index2) in item.wechatReplyKeywordList">
            <el-tag size="mini" v-if="wordItem.wordkeyEq!==''" :key="index2+'wordItem.wordkeyEq'" style="margin-left:5px;">{{wordItem.wordkeyEq}}</el-tag>
            <el-tag size="mini" v-if="wordItem.wordkeyLike!==''" :key="index2+'wordItem.wordkeyLike'" style="margin-left:5px;">{{wordItem.wordkeyLike}}</el-tag>
          </template>
          <div>
            <span>回复类型：</span>
            <span v-if="item.msgType ==='image'">图片</span>
            <span v-if="item.msgType ==='text'">文本</span>
            <span v-if="item.msgType ==='news'">图文</span>
            <span v-if="item.msgType ==='voice'">音乐</span>
            <span v-if="item.msgType ==='video'">视频</span>
            <span v-if="item.msgType ==='music'">音乐</span>
          </div>
        </div>
      </div>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" @click="setChange">确 定</el-button>
        <el-button type='info' size="mini" @click="handleClose">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { keywordPage } from '@/api/wechat/autoResponse.js'
export default {
  name: 'wxPictureZone',
  props: {
    size: [Number], // 控制选中的数量
    index: 0
  },
  mixins: [list],
  data() {
    return {
      queryParams: {
        page: 1,
        size: 10
      },
      tableData: [],
      checkedItems: [], // 选中图片的数组
      visible: false
    }
  },
  methods: {
    initData() {
      this.visible = true
      this.getPictureItems()
    },
    handleClose() {
      this.checkedItems = []
      this.visible = false
    },
    getPictureItems() {
      this.getTableData(keywordPage, this.queryParams)
      // keywordPage(this.queryParams).then(res => {
      //   if (res.code === 200) {
      //     this.checkedItems = [] // 重置全选数据
      //     this.total = res.data.totalElements
      //     this.tableData = res.data.content
      //   }
      // })
    },
    isChecked(item) {
      // 判断是否选中
      return this.checkedItems.findIndex(v => v.id === item.id) > -1
    },
    setCheckedItems(item, index) {
      this.checkedItems = [item]
    },
    setChange() {
      // 把选中的对象传递出来
      this.$emit('change', this.checkedItems, this.index)
      this.checkedItems = []
      this.visible = false
    }
  }
}
</script>

<style scoped lang='scss'>
.keyword-items--active {
  border: 1px solid #5cb85c;
  position: relative;
  &::before {
    content: "";
    width: 20px;
    height: 20px;
    background: url("./../../../../../assets/images/img-check.png");
    background-size: contain;
    position: absolute;
    top: 10px;
    right: 10px;
  }
}
</style>




