// 物流公司相关接口
import axios from './../fetch'

// 分页
export const logisticsCompanyPage = params => { return axios.get('/admin/platform/logisticscompany/page', { params: params }) }

// 详情
export const logisticsCompanyGet = params => { return axios.get('/admin/platform/logisticscompany/get', { params: params }) }
// 列表
export const logisticsCompanyList = params => { return axios.get('/admin/platform/logisticscompany/list', { params: params }) }

// 新增
export const logisticsCompanySave = params => { return axios.post('/admin/platform/logisticscompany/save', params) }
// 修改
export const logisticsCompanyEdit = params => { return axios.post('/admin/platform/logisticscompany/update', params) }
// 删除
export const logisticsCompanyDelete = params => { return axios.post('/admin/platform/logisticscompany/delete', params) }

// 排序
export const logisticsCompanySort = params => { return axios.post('/admin/platform/logisticscompany/sort', params) }

// 启用
export const logisticsCompanyEnable = params => { return axios.post('/admin/platform/logisticscompany/enable', params) }

// 禁用
export const logisticsCompanyDisable = params => { return axios.post('/admin/platform/logisticscompany/disable', params) }




