<template>
  <div>
 <el-select v-model="selectedOptions"  
   @change="handleChange" 
   :multiple="multiple"
    filterable size="mini"
    :multiple-limit="limit"
    >
   <el-option v-for="(item) in categoryItems" :key="item.id" :value="item.id" :label="item.cateName">
     <span :style="{paddingLeft:(item.level*18)+'px'}">
       <span v-if="item.level!=0">></span>
       {{item.cateName}}</span>
   </el-option>
 </el-select>
  </div>
</template>
<script>
import { productCategoryTree } from './../../api/product/category.js'
export default {
  props: {
    defaultData: {
      type: [String, Array]
    },
    multiple: {
      type: [Boolean],
      default: true
    },
    value: {
      type: [String, Array, Number]
    },
    limit: {// 多选的限制数量
      type: [Number],
      default: 0
    },
    parentId: {// 分类接口的父级id
      type: [Number, String],
      default: ''
    }
  },
  watch: {
    value: {
      handler() {
        this.selectedOptions = this.value
      },
      immediate: true,
      deep: true
    }
  },
  data() {
    return {
      selectedOptions: [],
      categoryItems: [] // 所有类目
    }
  },
  methods: {
    getCategoryItems(data, level) { // 递归获取到所有类目
      data.forEach(v => {
        const obj = {
          cateName: v.cateName,
          id: v.id,
          level: level
        }
        this.categoryItems.push(obj)
        if (v.childs && v.childs.length > 0) {
          this.getCategoryItems(v.childs, level + 1)
        }
      })
    },
    getCategoryData(parentId) {
      productCategoryTree({ parentId: parentId }).then(res => {
        this.getCategoryItems(res.data, 0)
      })
    },
    handleChange(value) {
      this.$emit('change', value)
      this.$emit('input', value)
    }
  },
  created() {
    this.getCategoryData(this.parentId)
  }
}
</script>

<style>
</style>



