// 店铺管理相关接口
import axios from './../fetch'

// 店铺列表分页
export const shopManageList = params => { return axios.get('/admin/platform/store/page', { params: params }) }

// 根据id获取单个店铺列表
export const shopManageGet = params => { return axios.get('/admin/platform/store/get', { params: params }) }

// 新增店铺
export const shopManageSave = params => { return axios.post('/admin/platform/store/save', params) }

// 修改店铺
export const shopManageUpdate = params => { return axios.post('/admin/platform/store/update', params) }

// 删除店铺
export const shopManageDelete = params => { return axios.post('/admin/platform/store/delete', params) }

// 验证店铺名
export const validName = params => { return axios.get('/admin/platform/store/validName', { params: params }) }

// 验证商家账号
export const validUsername = params => { return axios.get('/admin/platform/store/validUsername', { params: params }) }

// 验证手机联系人
export const validPhone = params => { return axios.get('/admin/platform/store/validPhone', { params: params }) }

// 验证邮箱
export const validContactMail = params => { return axios.get('/admin/platform/store/validContactMail', { params: params }) }

// 验证营业执照注册号
export const validBlNo = params => { return axios.get('/admin/platform/store/validBlNo', { params: params }) }

// 验证身份证
export const validIdcard = params => { return axios.get('/admin/platform/store/validIdcard', { params: params }) }

// 等级申请列表分页
export const shopLevelList = params => { return axios.get('/admin/platform/store/levPage', { params: params }) }

// 删除店铺申请等级
export const shopLevelDelete = params => { return axios.post('/admin/platform/store/delLevel', params) }

// 审核店铺等级
export const shopLevelAudit = params => { return axios.post('/admin/platform/store/audit', params) }

// 查看店铺商品分类
export const categorylist = params => { return axios.get('/admin/platform/storeProductCate/list', { params: params }) }
// 查看店铺商品分类树形结构
export const storeCategoryTree = params => { return axios.get('/admin/platform/storeProductCate/tree', { params: params }) }

// 新增店铺下级分类
export const savecategory = params => { return axios.post('/admin/platform/storeProductCate/save', params) }

// 获取店铺商品详情
export const categoryGet = params => { return axios.get('/admin/platform/storeProductCate/get', { params: params }) }

// 修改店铺商品分类
export const categoryUpdate = params => { return axios.post('/admin/platform/storeProductCate/update', params) }

// 删除店铺商品分类
export const categoryDelete = params => { return axios.post('/admin/platform/storeProductCate/delete', params) }

// 店铺商品分类列表排序
export const categorySort = params => { return axios.post('/admin/platform/storeProductCate/sort', params) }

// 入住申请列表
export const livepage = params => { return axios.get('/admin/platform/store/livepage', { params: params }) }

// 审核店铺
export const auditShop = params => { return axios.post('/admin/platform/store/audit', params) }

// 异步获取主营分类列表
export const productCategoryTree = params => { return axios.get('/admin/platform/productcategory/tree', { params: params }) }
// 获取会员信息
export const memberList = params => { return axios.get('/admin/platform/member/page', { params: params }) }

// 获取子节点数据集
export const artCategoryGetChild = params => { return axios.get('/admin/platform/articlecategory/getChild', { params: params }) }

// 改变店铺状态
export const setShopStatus = params => { return axios.post('/admin/platform/store/status', params) }
// 改变店铺审核机制
export const shopProductAudit = params => { return axios.post('/admin/platform/store/productaudit', params) }
// 改变店铺微信
export const shopWeChatEnable = params => { return axios.post('/admin/platform/store/wechatenable', params) }

// 检查会员邮箱是否存在
export const checkEmail = params => { return axios.get('/admin/platform/member/checkEmail', { params: params }) }



