// 模块统计公用的方法
import echarts from 'echarts'
import countTo from 'vue-count-to'
const moment = require('moment')
export default {
  components: {
    countTo
  },
  props: {
    timeData: {
      type: Array
    }
  },
  data() {
    return {
      chart: null, // 图表实例
      el: null, // 挂载的图标标识
      optionsData: {},
      loading: false,
      queryParams: {
        startTime: moment().subtract(7, 'days').format('YYYY-MM-DD'),
        endTime: moment().subtract(1, 'days').format('YYYY-MM-DD')
      }
    }
  },
  watch: {
    timeData: {
      handler() {
        this.queryParams.startTime = this.timeData[0]
        this.queryParams.endTime = this.timeData[1]
        this.getData()
      },
      deep: true
    }

  },
  methods: {
    getData() { }, // 只要复写这个函数就可以操作对应的图表数据,
    initChart() { // 初始化图表
      this.$nextTick(() => {
        if (this.el && this.el !== '') {
          this.chart = echarts.init(document.getElementById(this.el), 'walden')
        }
      })
    }
  },
  created() {
    this.getData()
  },
  mounted() { // 监听图表
    this.initChart()
    window.addEventListener('resize', () => {
      if (this.chart) {
        this.chart.resize()
      }
    })
  }

}



