// 文章分类相关接口
import axios from '../fetch'

// 获取子节点数据集
export const artCategoryTree = params => { return axios.get('/admin/platform/articlecategory/tree', { params: params }) }

// 新增分类
export const artCategorySave = params => { return axios.post('/admin/platform/articlecategory/save', params) }

// 修改分类
export const artCategoryEdit = params => { return axios.post('/admin/platform/articlecategory/update', params) }

// 获取详情
export const artCategoryGet = params => { return axios.get('/admin/platform/articlecategory/get', { params: params }) }

// 排序
export const artCategorySort = params => { return axios.post('/admin/platform/articlecategory/sort', params) }

// 删除
export const artCategoryDelete = params => { return axios.post('/admin/platform/articlecategory/delete', params) }

// 显示在底部导航
export const artCategoryShowBottom = params => { return axios.post('/admin/platform/articlecategory/showBottom', params) }

// 不显示在底部导航
export const artCategoryHideBottom = params => { return axios.post('/admin/platform/articlecategory/hideBottom', params) }

// 获取子节点数据集
export const artCategoryGetChild = params => { return axios.get('/admin/platform/articlecategory/getChild', { params: params }) }



