// 商城-会员等级相关接口
import axios from './../fetch'

// 商城会员等级列表分页
export const memberlevelList = params => { return axios.get('/admin/platform/memberlevel/page', { params: params }) }

// 商城会员等级列表详情
export const memberlevelGet = params => { return axios.get('/admin/platform/memberlevel/get', { params: params }) }

// 商城会员等级列表删除
export const memberlevelDelete = params => { return axios.post('/admin/platform/memberlevel/delete', params) }

// 商城会员等级列表新增
export const memberlevelAdd = params => { return axios.post('/admin/platform/memberlevel/save', params) }

// 商城会员等级列表修改
export const memberlevelUpdate = params => { return axios.post('/admin/platform/memberlevel/update', params) }

// 商城会员等级列表查询所有
export const memberlevelAll = params => { return axios.get('/admin/platform/memberlevel/list', { params: params }) }

// 检查等级会员名称是否存在
export const checkLevelName = params => { return axios.get('/admin/platform/memberlevel/checkLevelName', { params: params }) }



