// 定时任务接口
import axios from './../fetch'

// 定时任务-分页列表
export const jobPage = params => { return axios.get('/admin/platform/job/page', { params: params }) }

// 定时任务-单个
export const jobGet = params => { return axios.get('/admin/platform/job/get', { params: params }) }

// 定时任务-暂停任务
export const jobPause = params => { return axios.get('/admin/platform/job/pause', { params: params }) }

// 定时任务-暂停后开始
export const jobResume = params => { return axios.get('/admin/platform/job/resume', { params: params }) }

// 定时任务-重启单个任务
export const jobRefresh = params => { return axios.get('/admin/platform/job/refresh', { params: params }) }

// 定时任务-重启所有任务
export const jobRefreshAll = params => { return axios.get('/admin/platform/job/refreshAll', { params: params }) }

// 定时任务-检测job标识是否存在
export const jobCheckJobName = params => { return axios.get('/admin/platform/job/checkJobName', { params: params }) }

// 定时任务-获取job分组名
export const jobFindGroupName = params => { return axios.get('/admin/platform/job/findGroupName', { params: params }) }

// 定时任务-新增
export const jobSave = params => { return axios.post('/admin/platform/job/save', params) }

// 定时任务-修改
export const jobUpdate = params => { return axios.post('/admin/platform/job/update', params) }

// 定时任务-删除
export const jobDelete = params => { return axios.post('/admin/platform/job/delete', params) }

// 定时任务-启用
export const jobEnabledJobs = params => { return axios.post('/admin/platform/job/enabledJobs', params) }

// 定时任务-禁用
export const jobDisabledJobs = params => { return axios.post('/admin/platform/job/disabledJobs', params) }



