 
<template>
  <div>
    <div @click="initData">
      <slot>
        <cms-button>文本</cms-button>
      </slot>
    </div>
    <el-dialog title='文本编辑' :visible="dialogVisible" width="700px" :before-close="close" :append-to-body='true'>
      <div class="cms-dialog__body">
        <el-input maxlength="500" resize="none" type="textarea" :rows="12" v-model="content" placeholder="请输入回复内容"></el-input>
      </div>
       <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" :disabled="content===''"  @click="determine">确 定</el-button>
        <el-button type='info'  size="mini"   @click="close">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
//  wxTextZone
// import { wechatMaterialPage } from '@/api/wechat/index.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  name: 'videoZone',
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: false
    },
    labelName: {
      type: String,
      default: 'name'
    }
  },
  data() {
    return {
      dialogVisible: false,
      msgType: 'text',
      content: '',
      message: ''
    }
  },
  methods: {
    initData() {
      this.dialogVisible = true
      // this.getTableData(wechatMaterialPage, this.queryParams)
    },
    determine() {
      if (this.content === '') {
        return
      } else {
        this.$emit('change', { content: this.content, msgType: this.msgType })
        this.close()
      }
    },
    close(done) {
      this.content = ''
      this.dialogVisible = false
    }
  }
}
</script>

<style lang='scss' scoped>
</style>






