export default {
  data() {
    return {
      loading: false, // 遮罩
      queryParams: {}, // 查询参数对象
      tableData: null, // 表格数据
      callback: '', // 列表查询函数
      isPage: true,
      checkItems: [], // 被选中对象
      total: 0
    }
  },
  computed: {
    ids() { // 选中的id 数组 仅属性为id可用
      const arr = []
      this.checkItems.map(v => {
        arr.push(v.id)
      })
      return arr
    },
    batchStatus() { // 判断是否有选中默认
      if (this.checkItems.length > 0) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    query() { // 查询函数
      this.queryParams.page = 1
      this.tableData = []
      this.getTableData(this.callback, this.queryParams)
    },
    /**
     * 无特殊形式的获取表格
     * @param {Function} callback 请求方法
     * @param {Object} params 请求参数
     * @param {Boolean} isPage 是否有分页
     */
    getTableData(callback, params, isPage = true) {
      // this.tableData = []
      this.loading = true
      this.callback = callback
      this.isPage = isPage
      callback(params).then(res => {
        if (res.code === 200) {
          if (isPage) {
            this.total = res.data.totalElements
            this.tableData = res.data.content
          } else {
            this.tableData = res.data
          }
        } else {
          this.$errorMessage(res.message)
        }
        this.loading = false
      }).catch(res => {
        this.loading = false
        this.$errorMessage('加载失败，请重试！')
      })
    },
    getCheckItems(obj) { // 获取全选数据
      this.checkItems = obj
    },
    checkRows(row, event, column) { // 单击行选中
      this.$refs.dataTable.toggleRowSelection(row)
    },
    getPage(current, size) { // 翻页方法
      console.log(size)
      this.queryParams.page = current
      this.queryParams.size = size
      this.getTableData(this.callback, this.queryParams)
    },
    sortNum(el, params, status = false) {
      // 数字排序
      return false
    },
    deleteTableData(callback, id, message = '您确定删除此条数据吗') { // 删除参数为ids的使用当前方法
      this.$confirm(message, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        callback({ ids: [id] }).then(res => {
          if (res.code === 200) {
            this.$successMessage('删除成功')
            this.getTableData(this.callback, this.queryParams, this.isPage)
          } else {
            this.$errorMessage('删除失败,' + res.message)
          }
        })
      })
        .catch(() => {})
    },
    deleteTableDatas(callback, ids, message = '您确定批量删除数据吗') { // 删除参数为ids的使用当前方法
      if (this.batchStatus) {
        this.$confirm(message, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          callback({ ids: ids }).then(res => {
            if (res.code === 200) {
              this.$successMessage('删除成功')
              this.getTableData(this.callback, this.queryParams, this.isPage)
            } else {
              this.$errorMessage('删除失败,' + res.message)
            }
          })
        })
          .catch(() => {})
      } else {
        this.$errorMessage('至少选择一条数据')
      }
    }
  },
  mounted() {

  }
}



