// 文章分类相关接口
import axios from '../fetch'

// 获取详情
export const artManagerGet = params => { return axios.get('/admin/platform/article/get', { params: params }) }

// 获取详情
export const artManagerList = params => { return axios.get('/admin/platform/article/page', { params: params }) }

// 新增
export const artManagerSave = params => { return axios.post('/admin/platform/article/save', params) }

// 修改
export const artManagerEdit = params => { return axios.post('/admin/platform/article/update', params) }

// 排序
export const artManagerSort = params => { return axios.post('/admin/platform/article/sort', params) }

// 显示
export const artManagerShow = params => { return axios.post('/admin/platform/article/show', params) }

// 隐藏
export const artManagerHide = params => { return axios.post('/admin/platform/article/hide', params) }

// 获取文章分类详情
export const artCategory = params => { return axios.get('/admin/platform/articlecategory/tree', { params: params }) }

// 删除
export const adminDelete = params => { return axios.post('/admin/platform/article/delete', params) }




