// 商品管理相关接口
import axios from '../fetch'
import qs from 'qs'
// 商品列表
export const productPage = params => {
  return axios.get('/admin/platform/product/page', { params: params,
    paramsSerializer: function(params) {
      return qs.stringify(params, { arrayFormat: 'repeat' })
    }
  })
}
// 商品下架
export const productOffShelve = params => { return axios.post('/admin/platform/product/offShelve', params) }
// 商品上架,审核通过
export const productReviewPass = params => { return axios.post('/admin/platform/product/reviewPass', params) }

// 商品审核不通过
export const productReviewReject = params => { return axios.post('/admin/platform/product/reviewReject', params) }

// 商品待审核
export const productToCheck = params => { return axios.post('/admin/platform/product/tocheck', params) }

// 商品放入回收站
export const productRecyleDelete = params => { return axios.post('/admin/platform/product/recyle', params) }
// 商品还原
export const productRestore = params => { return axios.post('/admin/platform/product/restore', params) }

// 商品删除
export const productDelete = params => { return axios.post('/admin/platform/product/delete', params) }

// 获取商品详细信息

export const productGet = params => { return axios.get('/admin/platform/product/get', { params: params }) }

// 获取商品分类信息
export const productcategory = params => { return axios.get('/admin/platform/productcategory/list', { params: params }) }

// 商品新增
export const productSave = params => { return axios.post('/admin/platform/product/save', params) }

export const productUpdate = params => { return axios.post('/admin/platform/product/update', params) }

// // 商品新增自定义规格
export const customStandardItem = params => { return axios.post('/admin/platform/producttypeattr/saleSave', params) }
// 商品新增自定义规格属性项
export const customStandard = params => { return axios.post('/admin/platform/typeattrvalue/save', params) }
// 商家自定删除自定义规格项
export const customStandardDelete = params => { return axios.post('/admin/platform/typeattrvalue/delete', params) }
// 通过商品类型获取到商品属性信息
export const customAttribute = params => { return axios.post('/admin/platform/producttypeattr/nSaleSave', params) }

// 验证货号code唯一
export const ckItemNumber = params => { return axios.get('/admin/platform/product/ckItemNumber', { params: params }) }
// 运费模板列表
export const storeexpresstplList = params => { return axios.get('/admin/platform/storeexpresstpl/list', { params: params }) }




