<template>
  <div>
    <cms-sticky fixClass="tag__warpper--fixed">
      <div class="stat__header">
        <el-date-picker
          v-model="value"
          @change="getTime"
          value-format="yyyy-MM-dd"
          align="right"
          type="daterange"
          placeholder="选择日期"
          :picker-options="pickerOptions1"
          class="fl"
            :editable="false"
          :clearable="false"
        ></el-date-picker>
      </div>
    </cms-sticky>
  </div>
</template>

<script>
const moment = require('moment')
export default {
  data() {
    return {
      pickerOptions1: {
        disabledDate(time) {
          return time.getTime() > Date.now() - 86400000
        },
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()

              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              end.setTime(end.getTime() - 86400000)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              end.setTime(end.getTime() - 86400000)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              end.setTime(end.getTime() - 86400000)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      value: [
        moment()
          .subtract(7, 'days')
          .format('YYYY-MM-DD'),
        moment()
          .subtract(1, 'days')
          .format('YYYY-MM-DD')
      ]
    }
  },
  methods: {
    getTime(value) {
      this.$emit('change', value)
    }
  }
}
</script>

<style>
</style>



