
<template>
  <div>
    <el-input style="width:380px;cursor: pointer;" v-model="checkedNames" :readonly="true">
      <el-button @click="initData" slot="append" icon="el-icon-search"></el-button>
    </el-input>
    <el-dialog title='会员选择' :visible="dialogVisible" width="900px" :before-close="close">
      <div class="cms-dialog__body">
      <section class="cms-list-query">
          <div class="query-group">
            <label>会员账号</label>
            <el-input v-model="queryParams.username" @keyup.native.enter="query"></el-input>
          </div>
          <div class="query-group">
            <label>邮箱</label>
            <el-input v-model="queryParams.email" @keyup.native.enter="query"></el-input>
          </div>
          <div class="query-group">
            <label>手机</label>
            <el-input v-model="queryParams.phone" @keyup.native.enter="query"></el-input>
          </div>
          <div class="query-group">
             <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
          </div>
        </section>
      <el-table :data="tableData" style="cursor: pointer;" ref='dataTable'
      @selection-change='getCheckItems'
      row-key="id"
       @row-click='checkRows' max-height='500'>
        <!-- 多选 -->
        <el-table-column width="60"  type="selection" v-if="multiple" :reserve-selection="true">
        </el-table-column>
        <!-- 单选 -->
        <el-table-column width="60" align="center" v-else>
          <div slot-scope="scope">
            <el-radio :label="scope.$index" v-model="selectIndex" >&nbsp;</el-radio>
          </div>
        </el-table-column>
        <el-table-column prop="id" label="会员ID" width="85" align="center"></el-table-column>
        <el-table-column prop="username" label="会员账号" width="160" align="center"></el-table-column>
        <el-table-column prop="email" label="邮箱" min-width="160" align="center"></el-table-column>
        <el-table-column prop="phone" label="手机" min-width="150" align="center"></el-table-column>
        <el-table-column prop="memberLevel" label="会员等级" min-width="150" align="center">
          <div slot-scope="scope">
            <span v-if="scope.row.memberLevel">{{scope.row.memberLevel.levelName}}</span>
          </div>
        </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <cms-button type="primary" @click="determine">确 定</cms-button>
        <cms-button type='info' @click="close">取 消</cms-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>

import { memberList } from '@/api/users/user.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  name: 'cmsMember',
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: true
    },
    labelName: {
      type: String,
      default: 'name'
    }
  },
  data() {
    return {
      dialogVisible: false,
      checkItems: [],
      selectIndex: '', // 全选状态，默认全不选
      queryParams: {
        page: 1,
        size: 10,
        username: '',
        email: '',
        phone: ''
      },
      checkedNames: '' // 临时显示的名称
    }
  },
  methods: {
    initData() {
      this.dialogVisible = true
      this.getTableData(memberList, this.queryParams)
    },
    checkRows(row) { // 单击行选中
      if (this.multiple) { // 多选时
        this.$refs.dataTable.toggleRowSelection(row)
      } else { // 单选时
        this.selectIndex = this.tableData.indexOf(row)
        this.checkItems = [row]
      }
    },
    determine() {
      this.$emit('get', this.checkItems)
      if (this.multiple) { // 多选时
        this.$emit('input', this.checkItems.map(v => v.id))
      } else { // 单选时
        this.$emit('input', parseInt(this.checkItems.map(v => v.id).toString()))
      }
      this.checkedNames = this.checkItems.map(v => v[this.labelName]).toString()
      this.close()
    },
    close(done) {
      // this.checkItems = []
      // this.selectIndex = ''
      // this.$refs.dataTable.clearSelection()
      this.dialogVisible = false
    }
  }
}
</script>

<style lang='scss' scoped>
</style>



