// 消息模板设置相关接口
import axios from './../fetch'

// id查询
export const messageTplGet = params => { return axios.get('/admin/platform/messageTpl/get', { params: params }) }

// 详细模板信息列表（分页）
export const messageTpList = params => { return axios.get('/admin/platform/messageTpl/page', { params: params }) }

// 修改模板信息
export const messageTpUpdate = params => { return axios.post('/admin/platform/messageTpl/update', params) }

// 新增模板信息
export const messageTpSave = params => { return axios.post('/admin/platform/messageTpl/save', params) }

// 详细模板信息列表（分页）
export const updateIsOpen = params => { return axios.get('/admin/platform/messageTpl/updateIsOpen', { params: params }) }

// 校验模版标识是否唯一
export const checkMesCode = params => { return axios.post('/admin/platform/messageTpl/checkMesCode', params) }



