import axios from './../fetch'
import qs from 'qs'
// 获取退换货列表分页
export const storesettlementPage = params => {
  return axios.get('/admin/platform/refund/page', { params: params,
    paramsSerializer: function(params) {
      return qs.stringify(params, { arrayFormat: 'repeat' })
    }
  })
}

// 获取订单列表
export const orderPage = params => { return axios.get('/admin/platform/order/page', { params: params }) }
// 获取订单详细
export const orderGet = params => { return axios.get('/admin/platform/order/get', { params: params }) }
// 获取售后列表
export const refundPage = params => { return axios.get('/admin/platform/refund/page', { params: params }) }
// 查看售后详细
export const refundGet = params => { return axios.get('/admin/platform/refund/get', { params: params }) }
// 售后审核
export const refundCheck = params => { return axios.post('/admin/platform/refund/check', params) }
// 查看售后协商记录
export const getConsultRecord = params => { return axios.get('/admin/platform/refund/getConsultRecord', { params: params }) }
// 售后-确认收货
export const refundReceive = params => { return axios.post('/admin/platform/refund/receive', params) }
// 查看物流信息
export const orderFlowInfo = params => { return axios.get('/admin/platform/orderflow/info', { params: params }) }
// 订单售后--查看物流信息
export const refundFlowInfo = params => { return axios.get('/admin/platform/refund/refundFlowInfo', { params: params }) }
// -取消订单
export const orderCancel = params => { return axios.get('/admin/platform/order/cancel', { params: params }) }
// -同步订单
export const orderSync = params => { return axios.get('/admin/platform/order/sync', { params: params }) }
// -退换货导出
export const refundExport = params => {
  return axios.get('/admin/platform/refund/export', { params: params,
    paramsSerializer: function(params) {
      return qs.stringify(params, { arrayFormat: 'repeat' })
    },
    responseType: 'blob'
  })
}



