// 相关接口
import axios from '../fetch'

// 异步获取分类列表
export const productCategoryList = params => { return axios.get('/admin/platform/productcategory/getChild', { params: params }) }

export const productCategoryDelete = params => { return axios.post('/admin/platform/productcategory/delete', params) }

// 新增分类
export const productCategorySave = params => { return axios.post('/admin/platform/productcategory/saveChild', params) }

// 获取详情
export const productCategoryGet = params => { return axios.get('/admin/platform/productcategory/get', { params: params }) }
// 修改分类
export const productCategoryUpdate = params => { return axios.post('/admin/platform/productcategory/update', params) }

// 异步获取分类列表
export const productCategoryTree = params => { return axios.get('/admin/platform/productcategory/tree', { params: params }) }

// 获取分类导航信息
export const productCategoryNavGet = params => { return axios.get('/admin/platform/productcategorynavi/get', { params: params }) }

// 修改分类导航信息
export const productCategoryNavUpdate = params => { return axios.post('/admin/platform/productcategorynavi/update', params) }

// 修改分类导航信息显示
export const productCategoryShow = params => { return axios.post('/admin/platform/productcategory/show', params) }

// 修改分类导航信息隐藏
export const productCategoryHide = params => { return axios.post('/admin/platform/productcategory/hide', params) }

// 修改分类导航信息虚拟
export const productCategoryVirtual = params => { return axios.post('/admin/platform/productcategory/virtual', params) }

// 修改分类导航信息非虚拟
export const productCategoryDisVirtual = params => { return axios.post('/admin/platform/productcategory/disVirtual', params) }

// 分类导航信息排序
export const productCategorySort = params => { return axios.post('/admin/platform/productcategory/sort', params) }




