// occ云存储相关接口
import axios from './../fetch'

// 分页
export const occPage = params => { return axios.get('/admin/platform/oss/page', { params: params }) }
// 获取详情
export const occGet = params => { return axios.get('/admin/platform/oss/get', { params: params }) }

// 新增
export const occSave = params => { return axios.post('/admin/platform/oss/save', params) }

// 修改
export const occEdit = params => { return axios.post('/admin/platform/oss/update', params) }

// 删除
export const occDelete = params => { return axios.post('/admin/platform/oss/delete', params) }



