// 小程序设置相关接口
import axios from './../fetch'
// 查询小程序列表
export const judgeMiniprogram = params => { return axios.get('/admin/platform/UserStatistics/judgeMiniprogran', { params: params }) }
// 查询小程序列表
export const pageMiniprogram = params => { return axios.get('/admin/platform/UserStatistics/pageMiniprogran', { params: params }) }

// 版本管理开始
// 新增一个开发版本
export const versionAddSubmit = params => { return axios.post('/admin/platform/miniprogramversion/addSubmit', params) }
// 获取一个开发版本
export const newVersionGet = params => { return axios.get('/admin/platform/miniprogramversion/getNewVersion', { params: params }) }
// 新增一个审核版本
export const versionAddAudit = params => { return axios.post('/admin/platform/miniprogramversion/addAudit', params) }
// 获取审核记录
export const auditVersionGet = params => { return axios.get('/admin/platform/miniprogramversion/getAudit', { params: params }) }

// 获取页面配置
export const versionPageConfig = params => { return axios.get('/admin/platform/miniprogramversion/getPage', { params: params }) }
// 获取可选类目
export const versionGetCategory = params => { return axios.get('/admin/platform/miniprogramversion/getCategory', { params: params }) }
// 撤销审核
export const versionDeleteAudit = params => { return axios.post('/admin/platform/miniprogramversion/deleteAudit', params) }

// 新增一个发布版本
export const versionAddRelease = params => { return axios.post('/admin/platform/miniprogramversion/addRelease', params) }
// 是否显示按钮
export const versionIsShow = params => { return axios.get('/admin/platform/miniprogramversion/isShow', { params: params }) }
// 获取发布版本
export const releaseVersionGet = params => { return axios.get('/admin/platform/miniprogramversion/getRelease', { params: params }) }
// 修改发布版本状态
export const updateReleaseStatus = params => { return axios.post('/admin/platform/miniprogramversion/updateReleaseStatus', params) }
// 获取体验二维码
export const getQrcode = params => { return axios.get('/admin/platform/miniprogramversion/getQrcode', { params: params }) }

// 小程序代码管理开始
// 查询草稿箱的数据
export const templateDraftPageDraft = params => { return axios.get('/admin/platform/templateDraft/pageDraft', { params: params }) }
// 同步草稿箱的数据
export const templateDraftSynchronousDraft = params => { return axios.get('/admin/platform/templateDraft/synchronousDraft', { params: params }) }
// 草稿箱的草稿添加入模板库
export const templateDraftAddTemplate = params => { return axios.post('/admin/platform/templateDraft/addTemplate', params) }
// 查询模板库的数据
export const templateDraftPageTemplate = params => { return axios.get('/admin/platform/templateDraft/pageTemplate', { params: params }) }
// 同步模板库的数据
export const templateDraftSynchronousTemplate = params => { return axios.get('/admin/platform/templateDraft/synchronousTemplate', { params: params }) }
// 模板库的模板设为最新版本
export const templateDraftUpdateTemplate = params => { return axios.post('/admin/platform/templateDraft/updateTemplate', params) }
// 删除模板库的模板
export const templateDraftDeleteTemplate = params => { return axios.post('/admin/platform/templateDraft/deleteTemplate', params) }
// 小程序代码管理结束

// 体验者管理开始
// 新增一个体验者
export const miniProgramMemberAdd = params => { return axios.post('/admin/platform/miniprogrammember/add', params) }
// 获取所有体验者
export const miniProgramMemberFindAll = params => { return axios.get('/admin/platform/miniprogrammember/findAll', { params: params }) }
// 解绑一个体验者
export const miniProgramMemberDelete = params => { return axios.post('/admin/platform/miniprogrammember/delete', params) }
// 体验者管理结束

// 显示服务器域名
export const UserStatisticsDisplayModifyDomain = params => { return axios.get('/admin/platform/UserStatistics/displayModifyDomain', { params: params }) }
// 重置服务器域名
export const UserStatisticsModifyDomain = params => { return axios.get('/admin/platform/UserStatistics/modifyDomain', { params: params }) }



