 
<template>
  <div>
    <div @click="initData">
      <slot>
        <cms-button>音乐</cms-button>
      </slot>
    </div>
    <el-dialog title='音乐编辑' :visible="dialogVisible" width="700px" :before-close="close" :append-to-body='true'>
      <div class="cms-dialog__body">
        <el-form :model="formData" status-icon ref="form" label-width="150px" class="demo-ruleForm">
          <el-form-item label="音乐标题">
            <el-input type="text" maxlength="150" v-model="formData.title" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="音乐链接">
            <el-input  type="text" maxlength="255" v-model="formData.musicUrl" autocomplete="off">
              <cms-picture-zone  slot="append" type='music' viewType='list' @change="getAudio">
                <el-button style="position:relative;top:5px;" size='small'>选择音乐文件</el-button>
              </cms-picture-zone>
            </el-input>
            <div class="gray">选择上传的音频文件或直接输入URL地址，常用格式：mp3</div>
          </el-form-item>
          <el-form-item label="高品质音乐链接">
            <el-input maxlength="255" v-model.number="formData.hqMusicUrll"></el-input>
            <div class="gray">高质量音乐链接，WIFI环境优先使用该链接播放音乐</div>
          </el-form-item>
          <el-form-item label="描述">
            <el-input maxlength="30" v-model.number="formData.description"></el-input>
            <div class="gray">输入简短的音乐描述，30字以内</div>
          </el-form-item>
          <el-form-item label="图片" :required="true">
            <image-zone class="wx-create__item" @change="getImages">
              <div class="wx-create__item">
                <p class="t-icon"><img src="./../../../../../assets/images/icon-img.png" alt=""></p>
                <p class="wx-create__title">选择图片</p>
              </div>
            </image-zone>
          </el-form-item>
          <el-form-item  v-if="params">
            <div class="media-preview__media t-icon" >
              <img :src="params.url" class="media-preview__img">
              <p  style="margin-left:13px;" class="media-preview__cell">{{params.name}}</p>
            </div>
          </el-form-item>
        </el-form>
      </div>
       <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" :disabled="formData.thumbMediaId===''" @click="determine">确 定</el-button>
        <el-button type='info'  size="mini"   @click="close">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
//  wxTextZone
import form from '@/mixins/form'
import imageZone from './wxPictureZone'
export default {
  mixins: [form],
  name: 'wxMusicZone',
  components: {
    imageZone
  },
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: false
    },
    labelName: {
      type: String,
      default: 'name'
    }
  },
  data() {
    return {
      dialogVisible: false,
      formData: {
        hqMusicUrll: '',
        musicUrl: '',
        title: '',
        description: '',
        msgType: 'music',
        thumbMediaId: ''
      },
      params: null
    }
  },
  methods: {
    getImages(items) { // 获取图片信息
      this.params = {
        url: this.$cms.weChatUrl(items[0].request.url),
        name: items[0].request.title
      }
      this.formData.thumbMediaId = items[0].id
    },
    getAudio(item, index) {
      if (item[0].url.indexOf('h') === 0) {
        this.formData.musicUrl = item[0].url
      } else {
        this.formData.musicUrl = process.env.baseUrl + item[0].url
      }
    },
    initData() {
      this.dialogVisible = true
      // this.getTableData(wechatMaterialPage, this.queryParams)
    },
    determine() {
      this.$emit('change', this.formData)
      this.close()
    },
    close(done) {
      this.formData = {
        hqMusicUrll: '',
        musicUrl: '',
        title: '',
        description: '',
        msgType: 'music',
        thumbMediaId: ''
      }
      this.dialogVisible = false
    }
  }
}
</script>

<style lang='scss' scoped>
.t-icon{
    height: 30px;
    text-align: center;
    img{
      max-height: 100%;
    }
  }
  .wx-create__title{
    font-size: 12px;

  }
</style>






