
// 微信自动回复相关接口
import axios from './../fetch'

// 获取关键词单个
export const keywordGet = params => { return axios.get('/admin/platform/wechatreplycontent/get', { params: params }) }

// 新增关键词
export const keywordSave = params => { return axios.post('/admin/platform/keyword/save', params) }

// 修改关键词
export const keywordUpdate = params => { return axios.post('/admin/platform/wechatreplycontent/update', params) }
// 显示关键词
export const keywordList = params => { return axios.get('/admin/platform/keyword/list', { params: params }) }

// 保存关键词（默认，关注）
export const keyclickSave = params => { return axios.post('/admin/platform/wechatreplyclick/save', params) }

// 删除关键词（默认，关注）
export const keyclickDelete = params => { return axios.post('/admin/platform/wechatreplycontent/delete', params) }
// 显示关键词（默认，关注）
export const keyclickGEt = params => { return axios.get('/admin/platform/wechatreplyclick/get', { params: params }) }
// 分页显示关键词
export const keywordPage = params => { return axios.get('/admin/platform/wechatreplycontent/page', { params: params }) }
// 批量删除关键字
export const keywordDelete = params => { return axios.post('/admin/platform/wechatreplycontent/delete', params) }




