// 商城-会员相关接口
import axios from './../fetch'

// 商城会员列表分页
export const memberList = params => { return axios.get('/admin/platform/member/page', { params: params }) }

// 商城会员详情
export const memberGet = params => { return axios.get('/admin/platform/member/get', { params: params }) }

// 商城会员删除
export const memberDelete = params => { return axios.post('/admin/platform/member/delete', params) }

// 商城会员修改
export const memberUpdate = params => { return axios.post('/admin/platform/member/update', params) }

// 商城会员新增
export const memberSave = params => { return axios.post('/admin/platform/member/save', params) }

// 商城会员启用
export const memberEnable = params => { return axios.post('/admin/platform/member/memberEnable', params) }

// 商城会员禁用
export const memberDisable = params => { return axios.post('/admin/platform/member/memberDisable', params) }

// 会员账户详情列表分页
export const memberAccountDetails = params => { return axios.get('/admin/platform/memberaccountdetails/page', { params: params }) }

// 会员成长值列表分页
export const memberGrowthDetails = params => { return axios.get('/admin/platform/membergrowthdetails/page', { params: params }) }

// 会员积分详情列表分页
export const memberScoreDetails = params => { return axios.get('/admin/platform/memberscoredetails/page', { params: params }) }

// 会员账户详情删除
export const memberAccountDetailsDelete = params => { return axios.post('/admin/platform/memberaccountdetails/delete', params) }

// 会员成长值删除
export const memberGrowthDetailsDelete = params => { return axios.post('/admin/platform/membergrowthdetails/delete ', params) }

// 会员积分详情删除
export const memberScoreDetailsDelete = params => { return axios.post('/admin/platform/memberscoredetails/delete ', params) }

// 检查会员名称是否存在
export const checkUserName = params => { return axios.get('/admin/platform/member/checkUsername', { params: params }) }

// 检查会员邮箱是否存在
export const checkEmail = params => { return axios.get('/admin/platform/member/checkEmail', { params: params }) }

// 检查会员电话是否存在
export const checkPhone = params => { return axios.get('/admin/platform/member/checkPhone', { params: params }) }

// 会员第三方账户信息-->添加
export const memberThirdSave = params => { return axios.post('/admin/platform/memberthird/save', params) }

// 会员第三方账户信息-->删除
export const memberThirdDelete = params => { return axios.post('/admin/platform/memberthird/delete', params) }



