// 店铺账户相关接口
import axios from '../fetch'

// 店铺账户列表（分页）
export const accountList = params => { return axios.get('/admin/platform/storeAccount/page', { params: params }) }

// 店铺账户明细列表（分页）
export const detailPage = params => { return axios.get('/admin/platform/storeAccount/detailPage', { params: params }) }

// 提交资金调整
export const accountUpdate = params => { return axios.post('/admin/platform/storeAccount/update', params) }

// 账户列表提现分页
export const stoPrePage = params => { return axios.get('/admin/platform/storeAccount/stoPrePage', { params: params }) }

// 账户列表充值分页
export const stoRecPage = params => { return axios.get('/admin/platform/storeAccount/stoRecPage', { params: params }) }
// 充值删除
export const rechargeDelete = params => { return axios.post('/admin/platform/storeAccount/deleteRec', params) }
// 获取充值结果
export const queryResult = params => { return axios.get('/admin/platform/storeAccount/search', { params: params }) }

// 审核提现账户
export const stoPreAudit = params => { return axios.post('/admin/platform/storeAccount/audit', params) }

// 删除提现账户
export const stoPreDelete = params => { return axios.post('/admin/platform/storeAccount/delete', params) }

// 资金调整
export const accountSelectGet = params => { return axios.get('/admin/platform/storeAccount/select', { params: params }) }



