
export default {
  data() {
    return {
      status: true,
      loading: false, // 遮罩
      type: this.$route.query.type, // 表单操作类型  save 新增 update 修改
      id: this.$route.query.id, // 从路由传递过来的id，仅限后台接收字段为id使用，请灵活使用
      errorMessage: {}, // 动态错误信息对象
      timer: null // 测试用定时器

    }
  },
  methods: {
    destroyed() { // 手动重载
      this.status = false
      setTimeout(() => {
        this.status = true
      }, 1)
    },
    validateForm() {
      // 判断是否全部验证通过
      const state = false

      return state
    },
    /** 异步回调，支持成功后继续操作
     * 普通表单的提交公用方法
     * @param {Function} dataMethod 提交的方法
     * @param {Object} data   提交的参数
     * @param {Boolean} state  是否有返回页
     * @param {String} path   返回的路径
     * @param {String} type    返回的类型
     * @param {Object} params  返回的参数
     */
    saveForm(dataMethod, data, state, path, type, params = {}) {
      return new Promise(resolve => {
        this.$refs.form.validate((valid, item) => {
          if (valid) {
            this.loading = true
            dataMethod(data).then(res => {
              this.loading = false
              if (res.code === 200) {
                this.$successMessage('添加成功')
                if (state) {
                  setTimeout(() => {
                    this.rest()
                    this.$routerLink(path, type, params)
                  }, 400)
                } else {
                  this.rest()
                }
                resolve(res)
              } else if (res.code === 100) { // 拦截具体的错误，防止前台遗漏
                let name = ''
                for (const key in res.data) {
                  name = key
                  break
                }
                this.errorPosition(name, res.data)
              } else {
                this.$errorMessage(res.code + ':' + res.message)
                this.loading = false
              }
            })
          } else { // 定位到错误的那一行
            this.loading = false
            let name = ''
            for (const key in item) {
              name = key
              break
            }
            this.errorPosition(name)
          }
        })
      })
    },
    /**
     * 普通表单的修改编辑公用方法
     * @param {Function} dataMethod 提交的方法
     * @param {Object} data   提交的参数
     * @param {String} path   返回的路径
     * @param {String} type    返回的类型
     * @param {Object} params  返回的参数
     */
    updateForm(dataMethod, data, path, type, params = {}, state = true) {
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          this.loading = true
          dataMethod(data).then(res => {
            this.loading = false
            if (res.code === 200) {
              this.$successMessage('修改成功')
              if (state) {
                setTimeout(() => {
                  this.rest()
                  this.$routerLink(path, type, params)
                }, 700)
              }
            } else if (res.code === 100) { // 拦截具体的错误，防止前台遗漏
              this.loading = false
              this.errorMessage = {}
              setTimeout(() => {
                for (const key in res.data) {
                  this.$set(this.errorMessage, key, res.data[key])
                }
              }, 5)
            } else {
              this.loading = false
              this.$errorMessage(res.code + ':' + res.message)
            }
          })
        } else { // 定位到错误的那一行
          this.loading = false
          let name = ''
          for (const key in item) {
            name = key
            break
          }
          this.errorPosition(name)
        }
      })
    },
    errorPosition(name, errorInfo) {
      // 错误信息定位
      const lay = document.querySelector('#layout')
      if (this.$refs[name]) {
        const ele = this.$refs[name].$el
        lay.scrollTop = ele.offsetTop - 350
      }
      this.$refs['form'].validateField(name)
      this.validateError(errorInfo)
      return false
    },
    autoFocus() {
      this.$refs.form.clearValidate()
      // this.$refs.first.focus()
    },
    rest() {
      window.clearInterval(this.timer)
      this.autoFocus()
      this.$refs.form.resetFields()
      this.destroyed()
      // this.formData = Object.assign({}, defaultData)
    },
    validateError(data) { // 错误信息对象
      this.errorMessage = {}
      setTimeout(() => {
        for (const key in data) {
          this.$set(this.errorMessage, key, data[key])
        }
      }, 1)
    }
  }
}



