import { refundGet, refundFlowInfo } from '@/api/order/index.js'
export default{
  data() {
    return {
      tableData: '',
      loading: false,
      refundDetails: [],
      buyerFlowList: [], // 买家物流信息
      sellerFlowList: []// 卖家物流信息
    }
  },
  filters: {
    refundStatusFilter(status) {
      const typeArr = ['待卖家确认', '退款中', '卖家拒绝', '', '待买家发货', '待卖家收货', '待买家收货', '待卖家发货', '已完成', '买家已取消']
      return typeArr[status]
    },
    refundTypeFilter(type) { // 收货服务类型过滤器
      const typeArr = ['仅退款', '退货退款', '换货']
      return typeArr[parseInt(type) - 1]
    },
    payModeFilter(type) { // 表格信息方式
      const modeArr = ['支付宝', '微信', '银联', '余额']
      return modeArr[parseInt(type) - 1]
    },
    platformStatusFilter(status) { // 平台介入处理状态
      const statusArr = ['待确定', '确认通过', '确认不通过']
      return statusArr[parseInt(status)]
    }
  },
  methods: {
    getTableData(id) {
      this.loading = true
      refundGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.tableData = res.data
          this.loading = false
          if (res.data.orderRefundDetails.length > 0) {
            var details = []
            res.data.orderRefundDetails.forEach(v => {
              details.push(v)
            })
            this.refundDetails = details.reverse()
          }
          if (res.data.orderRefund.status >= 5 && res.data.orderRefund.refundType > 1) {
            // 当状态为待卖家收货且类型为退款退货和换货时获取物流信息
            refundFlowInfo({ 'id': res.data.orderRefund.id }).then(flowInfo => { // 获取物流信息
              if (flowInfo.code === 200) {
                flowInfo.data.forEach(v => { // 1.卖家 2.买家
                  if (v.userType === 1) {
                    this.sellerFlowList = v.traces.reverse()
                  } else {
                    this.buyerFlowList = v.traces.reverse()
                  }
                })
              }
            })
          }
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    operateFilter(val) { // 售后流程
      var arr = ['提交申请', '卖家同意', '卖家拒绝', '申请撤回', '平台介入处理中', '买家已发货', '卖家确认收货', '卖家已发货', '买家确认收货', '修改售后申请', '', '', '平台审核通过', '平台审核拒绝']
      return arr[parseInt(val)]
    },
    changeService() {
      this.getTableData(this.$route.query.id)
    },
    checkService(item, status) {
      // 执行售后操作
      this.$refs.checkService.checkService(item, status)
    },
    showAttr(data) {
      // 转换json字符串属性
      if (data.length <= 0) {
        return ''
      } else {
        let str = ''
        data.forEach(v => {
          const tmp = v.attrName + ':' + v.attrValueName + '  '
          str = str + tmp
        })
        return str
      }
    }
  },
  created() {
    this.getTableData(this.$route.query.id)
  }
}



