// 消费者保障服务相关接口
import axios from './../fetch'

// 消费者保障服务列表(分页)
export const consumerServicesList = params => { return axios.get('/admin/platform/sysconsumerservice/page', { params: params }) }

// 保存
export const consumerServicesSave = params => { return axios.post('/admin/platform/sysconsumerservice/save', params) }

// 修改
export const consumerServicesEdit = params => { return axios.post('/admin/platform/sysconsumerservice/update', params) }

// 删除
export const consumerServicesDel = params => { return axios.post('/admin/platform/sysconsumerservice/delete', params) }

// 保存排序
export const consumerServicesSort = params => { return axios.post('/admin/platform/sysconsumerservice/sort', params) }

// 获取详情
export const consumerServicesGet = params => { return axios.get('/admin/platform/sysconsumerservice/get', { params: params }) }

// 是否启用
export const consumerServicesEnable = params => { return axios.post('/admin/platform/sysconsumerservice/enable', params) }



