// 微信资源上传
export const materialUpload = (params, callback1, callback2) => {
  return axios({
    url: '/admin/platform/wechatMaterial/saveMaterial',
    headers: { 'Content-Type': 'multipart/form-data' },
    method: 'post',
    onUploadProgress: function(progressEvent) { // 原生获取上传进度的事件
      if (progressEvent.lengthComputable) {
        // 属性lengthComputable主要表明总共需要完成的工作量和已经完成的工作是否可以被测量
        // 如果lengthComputable为false，就获取不到progressEvent.total和progressEvent.loaded
        callback1(progressEvent)
      }
    },
    data: params
  }).then(res => {
    callback2(res)
  })
}

// 微信视频资源上传
export const materialVideoUpload = (params, callback1, callback2) => {
  return axios({
    url: '/admin/platform/wechatMaterial/saveVideo',
    headers: { 'Content-Type': 'multipart/form-data' },
    method: 'post',
    onUploadProgress: function(progressEvent) { // 原生获取上传进度的事件
      if (progressEvent.lengthComputable) {
        // 属性lengthComputable主要表明总共需要完成的工作量和已经完成的工作是否可以被测量
        // 如果lengthComputable为false，就获取不到progressEvent.total和progressEvent.loaded
        callback1(progressEvent)
      }
    },
    data: params
  }).then(res => {
    callback2(res)
  })
}

