
<!--图片空间选择-->
<template>
  <div>
    <div  @click="initData">
      <slot>
        <el-button type="primary" size="mini">素材库选择</el-button>
      </slot>
    </div>
    <el-dialog :visible="visible" :before-close="handleClose" title="图文选择" :append-to-body='true'>
      <div style="max-height:500px;overflow:auto;">
      <div class="we-card__list we-card__list--dialog" v-loading='loading' >
      <div class="we-card__warpper" v-for="(content) in tableData" :key="content.id"
       @click="setCheckedItems(content)"
      >
        <template v-for="(item,index) in content.requestArray">
          <div class="we-card__item we-card__item--thumb" v-if="index==0" :key="index">
            <div  class="we-card__link">
              <img class="we-card__img" :src="$cms.weChatUrl(item.thumbMediaUrl)" alt="">
              <span class="we-card__title--thumb" v-if="item.title!=''">
                {{item.title}}
              </span>
            </div>
          </div>
          <div class="we-card__item  we-card__item--sub" v-else :key="index">
            <div  class="we-card__link">
              <div class="we-card__sub--inner">
                <div class="we-card__sub--img">
                  <img :src="$cms.weChatUrl(item.thumbMediaUrl)" alt="">
                </div>
                <div class="we-card__sub--title">
                  {{item.title}}
                </div>
              </div>
            </div>
          </div>
        </template>
        <div class="we-card__footer">
          <span class="fl">
            更新时间 {{$cms.getDateDiff(content.wechatUpdateTime*1000)}}
          </span>
        </div>
        <!-- 选中遮罩 -->
         <div class="we-card-checked" v-if="isChecked(content)">
           <i class="el-icon-check"></i>
         </div>
      </div>
    </div>
     </div>
      <cms-pagination @change="getPage"  :total='total' ></cms-pagination>
      
       <div class="el-dialog__footer">
          <el-button @click="setChange" type="primary" size="mini"  :disabled='checkedItems.length>0 ?false:true' >确定</el-button>
          <el-button @click="handleClose" type='info' size="mini" >取消</el-button>
        </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  wechatMaterialPage
} from '@/api/wechat/index.js'

export default {
  name: 'imageTextZone',
  props: {
    size: [Number], // 控制选中的数量
    index: 0
  },
  data() {
    return {
      params: {
        name: '',
        type: 'news',
        page: 1,
        size: 10
      },
      total: 0, // 图片总数
      baseUrl: process.env.baseUrl, // 相对路径拼接
      tableData: [],
      disable: true,
      checkAll: false,
      checkedItems: [], // 选中图片的数组
      selectedItem: {}, // 当前操作的图片对象
      selectedIndex: [], // 当前选中的是第几项
      visible: false,
      loading: false
    }
  },
  methods: {
    initData() {
      this.getTableData()
      this.visible = true
    },
    handleClose() {
      this.checkedItems = []
      this.visible = false
    },
    getTableData(params) {
      // 获取到点击的图片信息
      // this.tableData = []
      wechatMaterialPage(this.params).then(res => {
        if (res.code === 200) {
          this.checkedItems = [] // 重置全选数据
          this.total = res.data.totalElements
          this.tableData = res.data.content
        }
      })
    },
    isChecked(item) {
      // 判断是否选中
      return this.checkedItems.findIndex(v => v.id === item.id) > -1
    },
    setCheckedItems(item, index) {
      this.checkedItems = [item]
    },
    setChange() {
      // 把选中的对象传递出来
      this.$emit('change', this.checkedItems, this.index)

      this.checkedItems = []
      this.selectedItem = {} // 当前操作的图片对象
      this.selectedIndex = [] // 当前选中的是第几项
      this.visible = false
    },
    getPage(current, size) {
      // 分页调用
      this.params.page = current
      this.getTableData()
    }
  }
}
</script>





