// 运单模板相关接口
import axios from './../fetch'

// 分页
export const waybillTemplatePage = params => { return axios.get('/admin/platform/waybill/page', { params: params }) }

// 详情
export const waybillTemplateGet = params => { return axios.get('/admin/platform/waybill/get', { params: params }) }

// 新增
export const waybillTemplateSave = params => { return axios.post('/admin/platform/waybill/save', params) }

// 修改
export const waybillTemplateUpdate = params => { return axios.post('/admin/platform/waybill/update', params) }

// 删除
export const waybillTemplateDelete = params => { return axios.post('/admin/platform/waybill/delete', params) }

// 启用
export const waybillTemplateEnable = params => { return axios.post('/admin/platform/waybill/enable', params) }

// 禁用
export const waybillTemplateDisable = params => { return axios.post('/admin/platform/waybill/disable', params) }



