// 店铺等级相关接口
import axios from '../fetch'

// 店铺等级列表分页
export const gradeList = params => { return axios.get('/admin/platform/storeLevel/page', { params: params }) }

// 店铺等级修改
export const gradeUpdate = params => { return axios.post('/admin/platform/storeLevel/update', params) }

// 获取详情
export const gradeGet = params => { return axios.get('/admin/platform/storeLevel/get', { params: params }) }

// 店铺等级删除
export const gradeDalete = params => { return axios.post('/admin/platform/storeLevel/delete', params) }

// 保存店铺等级
export const gradeSave = params => { return axios.post('/admin/platform/storeLevel/save', params) }

// 保存排序
export const gradeSort = params => { return axios.post('/admin/platform/storeLevel/sort', params) }

// 获取等级权限
export const gradeMenus = params => { return axios.get('/admin/platform/storeLevel/menus', { params: params }) }

// 保存等级权限
export const saveGradeMenus = params => { return axios.post('/admin/platform/storeLevel/saveMenu', params) }



