<template>
  <div class="media-warpper">
    <div class="menu-msg-head">回复内容</div>
    <div class="menu-msg-panel">
      <div class="menu-msg-select_detail">
        <ul class="menu-msg-select__ul">
          <li>
            <!-- 未选择图文消息 -->
            <div class="wx-create">
              <image-text-zone class="wx-create__item" @change="getNews">
                <div class="wx-create__item">
                  <p class="t-icon"><img src="./../../../../../assets/images/icon-imgTxt.png" alt=""></p>
                  <p class="wx-create__title">图文</p>
                </div>
              </image-text-zone>
            </div>
          </li>
          <!-- 图片 -->
          <li>
            <div class="wx-create">
              <image-zone class="wx-create__item" @change="getImages">
                <div class="wx-create__item">
                  <p class="t-icon"><img src="./../../../../../assets/images/icon-img.png" alt=""></p>
                  <p class="wx-create__title">图片</p>
                </div>
              </image-zone>
            </div>

          </li>
          <li>
            <!-- 语音 -->
            <div class="wx-create">
              <audio-zone class="wx-create__item" @change="getVoice">
                <div class="wx-create__item">
                  <p class="t-icon"><img src="./../../../../../assets/images/icon-voice.png" alt=""></p>
                  <p class="wx-create__title">音频</p>
                </div>
              </audio-zone>
            </div>

          </li>
          <li>
            <!-- 视频 -->
            <div class="wx-create">
              <video-zone class="wx-create__item" @change="getVideo">
                <div class="wx-create__item">
                  <p class="t-icon"><img src="./../../../../../assets/images/icon-video.png" alt=""></p>
                  <p class="wx-create__title">视频</p>
                </div>
              </video-zone>
            </div>

          </li>
          <li v-if="show">
            <!-- 文本 -->
            <div class="wx-create">
              <wx-text-zone class="wx-create__item" @change="getTxt">
                <div class="wx-create__item">
                  <p class="t-icon"><img src="./../../../../../assets/images/icon-txt.png" alt=""></p>
                  <p class="wx-create__title">文本</p>
                </div>
              </wx-text-zone>
            </div>

          </li>
          <li v-if="show">
            <!-- 音乐 -->
            <div class="wx-create">
              <music-zone class="wx-create__item" @change="getMusic">
                <div class="wx-create__item">
                  <p class="t-icon"><img src="./../../../../../assets/images/icon-music.png" alt=""></p>
                  <p class="wx-create__title">音乐</p>
                </div>
              </music-zone>
            </div>

          </li>
        </ul>
        <!-- 回填 -->
        <div class="media-preview" v-if="preview">
          <div class="media-preview__media t-icon2" >
            <img :src="params.url" class="media-preview__img">
          </div>
          <div class="media-preview__inner">
            <div>
              <span>{{params.type}}</span>
              <p  style="margin-left:13px;" class="media-preview__cell">{{params.name}}</p>
            </div>
            <div style="margin-left:13px;" v-if="params.music || params.vmusic">
              <p v-if="params.music">音乐链接：{{params.music}}</p>
              <p v-if="params.vmusic">高品质音乐链接：{{params.vmusic}}</p>
            </div>
            <div style="margin-left:13px;" class="media-preview__content media-preview__cell">
              {{params.content}}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import imageTextZone from './wxImageTextZone'
import imageZone from './wxPictureZone'
import audioZone from './wxAudioZone'
import videoZone from './wxVideoZone'
import wxTextZone from './wxTextZone'
import musicZone from './wxMusicZone'
// import func from './vue-temp/vue-editor-bridge'
var videoImg = require('./../../../../../assets/images/video2.png')
var voiceImg = require('./../../../../../assets/images/voice.png')
export default {
  name: 'mediaWarpper',
  components: {
    imageTextZone,
    imageZone,
    audioZone,
    videoZone,
    wxTextZone,
    musicZone
  },
  props: {
    data: {
      type: [Object],
      default: function() {
        return {}
      }
    },
    show: {
      type: Boolean,
      default: function() {
        return false
      }
    },
    restData: {
      type: Boolean,
      default: function() {
        return false
      }
    }
  },
  data() {
    return {
      preview: false,
      type: false,
      params: {
        url: '',
        name: '',
        type: '',
        content: ''
      },
      news: [], // 显示的图文
      images: [],
      videos: [],
      voices: [],
      text: '',
      music: ''

    }
  },
  watch: {
    data: {
      handler(olds, news) {
        if (this.data && Object.keys(this.data).length > 0) {
          if (this.data.mediaType === 'news') {
            this.getNews([this.data])
          }
          if (this.data.mediaType === 'image') {
            this.getImages([this.data])
          }
          if (this.data.mediaType === 'video') {
            this.getVideo([this.data])
          }
          if (this.data.mediaType === 'voice') {
            this.getVoice([this.data])
          }
          if (this.data.mediaType === 'text') {
            this.getTxt(this.data)
          }
          if (this.data.mediaType === 'music') {
            this.getMusic(this.data)
          }
        } else {
          this.params = {
            url: '',
            name: '',
            type: '',
            content: ''
          }
          this.clear()
        }
      },
      deep: true,
      immediate: true
    },
    restData: {
      handler(olds, news) {
        if (olds) {
          this.params = {
            url: '',
            name: '',
            type: '',
            content: ''
          }
          this.clear()
        }
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    getTxt(items) {
      this.clear()
      this.type = 'text'
      this.params = {
        url: '',
        name: items.content,
        type: '【文本消息】',
        content: ''
      }
      this.preview = true
      this.$emit('change', items)
    },
    getMusic(items) {
      this.clear()
      this.type = 'music'
      this.params = {
        url: '',
        name: items.title,
        type: '【音乐消息】',
        music: items.musicUrl,
        vmusic: items.hqMusicUrll
      }
      this.preview = true
      this.$emit('change', items)
    },
    getImages(items) { // 获取图片信息
      this.clear()
      this.images = items
      this.type = 'image'
      this.params = {
        url: this.$cms.weChatUrl(items[0].request.url),
        name: items[0].request.title,
        type: '【图片消息】',
        content: ''
      }
      this.preview = true
      this.$emit('change', items)
    },
    getNews(items) { // 获取图文信息
      // 获取图文
      this.clear()
      this.news = items
      this.type = 'news'
      this.params = {
        url: this.$cms.weChatUrl(items[0].requestArray[0].thumbMediaUrl),
        name: items[0].requestArray[0].title,
        type: '【图文消息】',
        content: items[0].requestArray[0].digest
      }
      this.preview = true
      this.$emit('change', items)
    },
    getVoice(items) { // 获取音频信息
      this.clear()
      this.voices = items
      this.type = 'voice'
      this.params = {
        url: voiceImg,
        name: items[0].request.name,
        type: '【音频消息】',
        content: ''
      }
      this.preview = true
      this.$emit('change', items)
    },
    getVideo(items) { // 获取视频信息
      this.clear()
      this.videos = items
      this.type = 'video'
      this.params = {
        url: videoImg,
        name: items[0].request.name,
        type: '【视频消息】',
        content: ''
      }
      this.preview = true
      this.$emit('change', items)
    },
    clear() {
      this.images = []
      this.news = []
      this.videos = []
      this.voices = []
      this.text = ''
      this.music = ''
      this.preview = false
    }
  }
}
</script>

<style lang="scss" scoped>
  .t-icon{
    height: 30px;
    img{
      max-height: 100%;
    }
  }
  .t-icon2{
    height: 25px;
    img{
      max-height: 100%;
    }
  }
  .media-preview__content{
    word-wrap: break-word;
  }
</style>




