
<template>
  <div>
     <div @click="initData">
       <slot>
         <cms-button>音频选择</cms-button>
       </slot>
     </div>
    <el-dialog title='音频选择' :visible="dialogVisible" width="900px" :before-close="close" :append-to-body='true'>
      <div class="cms-dialog__body">
        <!-- <section class="cms-list-query">
          <div class="query-group">
            <label>名称</label>
            <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
          </div>
          <div class="query-group">
            <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
          </div>
        </section> -->
        <el-table :data="tableData" style="cursor: pointer;" ref='dataTable' @selection-change='getCheckItems' @row-click='checkRows' max-height='500'>
          <!-- 多选 -->
          <el-table-column width="60" type="selection" v-if="multiple">
          </el-table-column>
          <!-- 单选 -->
          <el-table-column width="60" align="center" v-else>
            <div slot-scope="scope">
              <el-radio :label="scope.$index" v-model="selectIndex">&nbsp;</el-radio>
            </div>
          </el-table-column>
          <el-table-column prop="request.name" label="名称" min-width="220">
          </el-table-column>
         
          <el-table-column label="创建时间" min-width="220">
            <template slot-scope="scope">
              {{$moment(scope.row.wechatUpdateTime*1000)}}
            </template>
          </el-table-column>
        </el-table>
        <cms-pagination @change="getPage" :total='total'></cms-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini"  @click="determine">确 定</el-button>
        <el-button type='info'  size="mini"   @click="close">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { wechatMaterialPage } from '@/api/wechat/index.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  name: 'audioZone',
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: false
    },
    labelName: {
      type: String,
      default: 'name'
    }
  },
  data() {
    return {
      dialogVisible: false,
      checkItems: [],
      selectIndex: '', // 全选状态，默认全不选
      queryParams: {
        name: '',
        type: 'voice',
        page: 1,
        size: 10
      },
      checkedNames: '' // 临时显示的名称
    }
  },
  methods: {
    initData() {
      this.dialogVisible = true
      this.getTableData(wechatMaterialPage, this.queryParams)
    },
    checkRows(row) {
      // 单击行选中
      if (this.multiple) {
        // 多选时
        this.$refs.dataTable.toggleRowSelection(row)
      } else {
        // 单选时
        this.selectIndex = this.tableData.indexOf(row)
        this.checkItems = [row]
      }
    },
    determine() {
      this.$emit('change', this.checkItems)
      if (this.multiple) {
        // 多选时
        this.$emit('input', this.checkItems.map(v => v.id))
      } else {
        // 单选时
        this.$emit('input', parseInt(this.checkItems.map(v => v.id).toString())
        )
      }
      this.checkedNames = this.checkItems.map(v => v[this.labelName]).toString()
      this.close()
    },
    close(done) {
      this.checkItems = []
      this.selectIndex = ''
      this.$refs.dataTable.clearSelection()
      this.dialogVisible = false
    }
  }
}
</script>

<style lang='scss' scoped>
</style>



