// 商城-api菜单管理相关接口
import axios from './../fetch'

// api分页数据
export const apiManagerList = params => { return axios.get('/admin/platform/apiManager/page', { params: params }) }

// api信息获取
export const apiManagerGet = params => { return axios.get('/admin/platform/apiManager/get', { params: params }) }

// api删除
export const apiManagerDelete = params => { return axios.post('/admin/platform/apiManager/delete', params) }

// api排序
export const apiManagerSort = params => { return axios.post('/admin/platform/apiManager/sort', params) }

// 新增api
export const apiManagerSave = params => { return axios.post('/admin/platform/apiManager/save', params) }

// 修改api
export const apiManagerUpdate = params => { return axios.post('/admin/platform/apiManager/update', params) }

// 验证api路径是否存在
export const apiCheckUrl = params => { return axios.get('/admin/platform/apiManager/checkUrl', { params: params }) }



