// 自营商店相关接口
import axios from '../fetch'

// 自营店铺列表分页
export const selfShopsList = params => { return axios.get('/admin/platform/selfShop/page', { params: params }) }

// 获取详情
export const selfShopsGet = params => { return axios.get('/admin/platform/selfShop/get', { params: params }) }

// 自营店铺修改
export const selfShopsUpdate = params => { return axios.post('/admin/platform/selfShop/update', params) }

// 自营店铺删除
export const selfShopsDelete = params => { return axios.post('/admin/platform/selfShop/delete', params) }

// 自营店铺新增
export const selfShopsSave = params => { return axios.post('/admin/platform/selfShop/save', params) }

// 验证姓名
export const selfVaildName = params => { return axios.get('/admin/platform/selfShop/validContactName', { params: params }) }

// 验证店家账号
export const checkUsername = params => { return axios.get('/admin/platform/member/checkUsername', { params: params }) }



